<template>
  <section class="umis-toolmaker-wrapper">
    <section
      class="umis-toolmaker__container"
      @click.capture.stop="handleClick"
    >
      <mis-schema
        v-if="schemaInstance"
        :key="updatedCount"
        :init-schema="schemaInstance"
      />
    </section>
    <section
      v-if="state.activeNode"
      class="umis-toolmaker__wedgets"
    >
      <el-button-group
        size="mini"
        class="umis-toolmaker__tools"
      >
        <el-button
          plain
          icon="Edit"
          type="primary"
          @click="onEdit"
        />
        <el-button
          :disabled="state.activeTrack === '/mis-page'"
          plain
          icon="Top"
          type="primary"
          @click="onTop"
        />
        <el-button
          :disabled="state.activeTrack === '/mis-page'"
          plain
          icon="Bottom"
          type="primary"
          @click="onBottom"
        />
        <el-button
          plain
          icon="View"
          type="primary"
          @click="onDebug"
        />
        <el-button
          :disabled="state.activeTrack === '/mis-page'"
          plain
          icon="Delete"
          type="primary"
          @click="onDelete"
        />
      </el-button-group>
    </section>
    <el-drawer
      v-model="state.drawerVisible"
      :title="`${state.chName}`"
      size="50%"
      destroy-on-close
      append-to-body
      lock-scroll
      custom-class="umis-toolmaker__drawer__container"
    >
      <div class="umis-toolmaker__drawer__content">
        <attrs-editor
          ref="propsEditorRef"
          :path="path"
          :mis-name="state.misName"
          :active-json="state.activeJson"
          :active-renderer="state.activeRenderer"
          :edit-tab-change="onEditTabChange"
          :node-change="onNodeChange"
        />
      </div>
      <div class="umis-toolmaker__drawer__footer">
        <el-button
          size="mini"
          type="primary"
          :loading="loading"
          @click="submitForm"
        >
          {{ loading ? ' ...' : ' ' }}
        </el-button>
      </div>
    </el-drawer>
    <el-dialog
      v-model="showDebug"
      title=""
      destroy-on-close
    >
      <mis-data
        class="umis-toolmaker-wrapper__debug"
        :path="`${path}/${state.activeTrack}`"
        :init-data="state.data"
      />
    </el-dialog>
  </section>
</template>
<script>
import {defineComponent, watch, getCurrentInstance, reactive, ref} from 'vue';
import {purifyObject} from '../../utils/tpl';
import useDropTools from '../../hooks/useDropTools';
import AttrsEditor from './attrs-editor.vue';
import componentMap from '../../hooks/useComponentMap';
const requireAttrs = require.context('../attrs', true, /[\w-]+\.js$/);
const misAttrs = {};

requireAttrs.keys().forEach(filePath => {
  const attrConfig = requireAttrs(filePath);
  let attrName = filePath.replace(/(.*\/)*([^.]+).*/gi, '$2');
  misAttrs[`mis-${attrName}`] = attrConfig;
});

export default defineComponent({
  name: 'MisToolmaker',
  components: {
    AttrsEditor,
  },
  setup(props) {
    const { proxy } = getCurrentInstance();
    const {findMisComponent, beforeJSONEdit, swapOneInJSON, schemaInstance} = useDropTools(props);
    const loading = ref(false);
    const showDebug = ref(false);
    const updatedCount = ref(0);
    const path = '/umis-editor/toolmaker';
    const state = reactive({
      activeTab: '',
      nodeType: 'body',
      activeRenderer: '',
      misName: '',
      chName: '',
      activeNode: '',
      drawerVisible: false,
      activeJson: {},
      activeTrack: '',
    });
    const handleClick = (e) => {
      const node = findMisComponent(e.target);
      const track = node.getAttribute('track');
      const path = node.getAttribute('path');
      track && proxy.$eventHub.$emit('mis-component:state', path, data => {
        state.data = data;
      });
      if (!track || state.activeNode == node) {
        onClear();
      } else if (state.activeTrack !== track) {
        state?.activeNode?.classList?.remove('umis-toolmaker__active');
        state.activeTrack = track;
        state.activeNode = node;
        state?.activeNode?.classList?.add('umis-toolmaker__active');
      }
    };
    const submitForm = () => {
      const { form } = proxy.$refs.propsEditorRef.$refs.normalFormRef.$refs;
      let pureForm = {};
      form.validate((valid) => {
        if (valid) {
          const { model } = form;
          for (let key in model) {
            if (model.hasOwnProperty(key)) {
              pureForm[key] = model[key];
            }
          }
          if (state.activeTab !== '5') {
            const defaultModel = {};
            proxy.$.appContext.$misAttrs[`mis-${state.misName}`].default.forEach(item => {
              defaultModel[item.name] = item.defaultValue;
            });
            Object.assign(defaultModel, pureForm)
          }
          Object.assign(state.activeJson, pureForm);
          purifyObject(state.activeJson, proxy.$.appContext.components[state.activeRenderer].mixins[0].props);
          updatedCount.value++;
          state.drawerVisible = false;
        }
      });
    };
    const onClear = () => {
      state?.activeNode?.classList?.remove('umis-toolmaker__active');
      state.nodeType = 'body';
      state.activeNode = '';
      state.activeTrack = '';
      state.data = {};
    };
    const onEditTabChange = (name) => {
      state.activeTab = name;
    };
    const onNodeChange = name => {
      state.nodeType = name;
    };
    const onEdit = () => {
      const misName = state.activeNode.getAttribute('track-id')
        .split('-')
        .map((kebab) => kebab.charAt(0).toUpperCase() + kebab.slice(1))
        .join('');

      if (!proxy.$.appContext.components[misName]) {
        proxy.$message('');
      }
      const { json } = beforeJSONEdit();
      state.activeJson = json;
      state.activeRenderer = misName;
      state.misName = misName.replace('Mis', '').toLowerCase();
      state.chName = componentMap[state.misName].chName;
      state.drawerVisible = true;
    };
    const onDebug = () => {
      showDebug.value = true;
    };
    const onBottom = () => {
      const { pJson, trackIndex } = beforeJSONEdit();
      if (trackIndex + 1 !== pJson.length) {
        swapOneInJSON(pJson, trackIndex + 1, trackIndex);
      } else {
        proxy.$message('');
      }
      onClear();
    };
    const onTop = () => {
      const { pJson, trackIndex } = beforeJSONEdit();
      if (trackIndex !== 0) {
        swapOneInJSON(pJson, trackIndex, trackIndex - 1);
      } else {
        proxy.$message('');
      }
      onClear();
    };
    const onDelete = () => {
      const { pJson, trackIndex } = beforeJSONEdit();
      pJson.splice(trackIndex, 1);
      onClear();
    };
    watch(() => state.drawerVisible, (val) => {
      if (!val) {
        onClear();
      }
    });
    proxy.$.appContext.$misAttrs = misAttrs;

    return {
      onEditTabChange,
      onNodeChange,
      submitForm,
      handleClick,
      onClear,
      onEdit,
      onDebug,
      onBottom,
      onTop,
      onDelete,
      componentMap,
      schemaInstance,
      updatedCount,
      showDebug,
      loading,
      state,
      path
    };
  },
});
</script>
